  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 3px 3px;
  opacity: 0;
  position: absolute;
  transition: all 0.3s ease 0.15s;
  visibility: hidden;
  width: 100%;
  left: 0;
  z-index: 2;
}
li:hover > .mega-menu {
  opacity: 1;
  overflow: visible;
  visibility: visible;
}
